import React , {useEffect , useState} from 'react'
import {useParams , useHistory} from 'react-router-dom';
import { Image, Item } from 'semantic-ui-react'
import Spinner from 'react-bootstrap/Spinner'
import Jumbotron from 'react-bootstrap/Jumbotron'
import Card from 'react-bootstrap/Card'
import Alert from 'react-bootstrap/Alert'
const Recipe = () => {
    const [info , setInfo] = useState(<div style={{display: "flex" , justifyContent: "center"}}><Spinner animation="border" variant="primary" /></div>)
    const [eq , setIq] = useState([])
    const Id = useParams()
    useEffect( () => {
        fetch(`https://api.spoonacular.com/recipes/${Id.id}/information?apiKey=10d8784b2602484d9a99925d2f103d4e`)
        .then(response => response.json())
        .then(response => {
            if(response.code !== 200) {
                return setInfo(
                    <div style={{display: "flex" , justifyContent: "center"}}>
                        <Alert variant='danger'>
                            looks like the api is not returning any results due to daily points limitation
                        </Alert>
                    </div>
                    
                )
            }
            let steps = [];
            for(let step of response.analyzedInstructions[0].steps) {
                steps.push(
                    <Card style={{ width: '18rem' }}>
                        <Card.Body>
                        <Card.Title>Step {step.number}</Card.Title>
                        <Card.Text style={{fontFamily: "serif"}}>
                            {step.step}
                        </Card.Text>
                        </Card.Body>
                    </Card>
                )
            }
            let sum = response.summary.replace(/<b>/g , "")
            setInfo(
                <>
                <Jumbotron fluid>
                    <h1>{response.title}</h1>
                <Item>
                    <Item.Image src={response.image} size="small" circular />
                    <br/>
                    <Item.Content>
                    <Item.Description>
                        {sum}
                    </Item.Description>
                    </Item.Content>
                </Item>
                </Jumbotron>
                <div style={{display: 'flex' , justifyContent: 'center' , flexWrap: 'wrap'}}>
                    {eq}
                </div>
                <br/>
                <div style={{display: 'flex' , justifyContent: 'center' , flexWrap: 'wrap'}}>
                {steps}
                </div>
                
                </>
            )
        })
    }, [])
    return(
        <div>
            {info}
        </div>
        
    )
}

export default Recipe