import React , {useEffect , useState} from 'react'
import {useParams , useHistory} from 'react-router-dom';
import Card from 'react-bootstrap/Card'
import {Button} from 'semantic-ui-react'
import Spinner from 'react-bootstrap/Spinner'
import Alert from 'react-bootstrap/Alert'
const Search = () => {
    const x = useParams();
    const history = useHistory();
    const [display , setDisplay] = useState(<div style={{display: "flex" , justifyContent: "center"}}><Spinner animation="border" variant="primary" /></div>)
    const select = (e) => {
        history.push(`/recipe=${e.target.id}`)
    }
    useEffect(() => {
        fetch(`https://api.spoonacular.com/recipes/complexSearch?apiKey=10d8784b2602484d9a99925d2f103d4e&query=${x.q}&number=50`)
        .then(response => response.json())
        .then(response => {
            if(response.code !== 200) {
                return setDisplay(
                    <Alert variant='danger'>
                        looks like the api is not returning any results due to daily points limitation
                    </Alert>
                )
            }
            let searchDisplay = [];
            for(let recipe of response.results) {
                searchDisplay.push(
                    <Card style={{ width: '18rem' }}>
                        <Card.Img variant="top" src={recipe.image} />
                        <Card.Body>
                        <Card.Title className="title">{recipe.title}</Card.Title>
                        <Button basic color='pink' content="Full recipe" id={recipe.id} onClick={select} />
                        </Card.Body>
                    </Card>
                )
            }
            setDisplay(searchDisplay);
        })
    }, [])
    return(
        <>
        <h1 style={{display: "flex" , justifyContent: "center"}} id="tx">Search results for {x.q}</h1>
        <div style={{display: "flex" , justifyContent: "center" , flexWrap: "wrap"}}>
            {display}
        </div>
        </>
    )
}

export default Search 