import React , {useState} from 'react'
import {BrowserRouter as Router , Link , Route , Switch , useHistory} from 'react-router-dom';
import Navbar from 'react-bootstrap/Navbar'
import Nav from 'react-bootstrap/Nav'
import Jumbotron from 'react-bootstrap/Jumbotron'
import Form from 'react-bootstrap/Form'
import FormControl from 'react-bootstrap/FormControl'
import {Button} from 'semantic-ui-react'
const Navigation = () => {
    const [query , setQuery] = useState(null)
    const history = useHistory()
    const handleChange = () => {
        const New = document.getElementById('bar').value
        setQuery(New)
        
    }
    const handleSearch = () =>{
        history.push(`/search=${query}`)
    }
    return(
        <>
        <Router>
        <Navbar expand="lg" style={{backgroundColor: '#dc63f7'}}>
        <Navbar.Toggle aria-controls="basic-navbar-nav" />
        <Navbar.Collapse id="basic-navbar-nav">
            <Form inline>
                <FormControl placeholder="Search recipes here" onChange={handleChange} id="bar"/>
                <Button icon="search" style={{height: '34px'}} onClick={handleSearch}/>
            </Form>
            <Nav style={{display: 'flex' , justifyContent: 'center'}}>
                <Nav.Link><Button variant="outline-danger">Diets</Button></Nav.Link>
                <Nav.Link>Wine</Nav.Link>
                <Nav.Link>Grocery Items</Nav.Link>
                <Nav.Link>Tips</Nav.Link>
            </Nav>
        </Navbar.Collapse>
        </Navbar>
        <br></br>
        </Router>
        </>
    )
}
export default Navigation