import React , {useEffect , useState} from 'react'
import {useHistory} from 'react-router-dom';
import Spinner from 'react-bootstrap/Spinner'
import Jumbotron from 'react-bootstrap/Jumbotron'
import Card from 'react-bootstrap/Card'
import {Button} from 'semantic-ui-react'
import Alert from 'react-bootstrap/Alert'
const Home = () => {
    const [display , setDisplay] = useState(<div style={{display: "flex" , justifyContent: "center"}}><Spinner animation="border" variant="primary" /></div>)
    const history = useHistory()
    const select = (e) => {
        history.push(`/recipe=${e.target.id}`)
    }
    
        
    useEffect(() => {
        fetch(`https://api.spoonacular.com/recipes/random?apiKey=10d8784b2602484d9a99925d2f103d4e&number=50`)
        .then(response => response.json())
        .then(response => {
            if(response.code !== 200) {
                return setDisplay(
                    <Alert variant='danger'>
                        looks like the api is not returning any results due to daily points limitation
                    </Alert>
                )
            }
            let display = []
            for(let recipe of response.recipes) {
                display.push(
                    <Card style={{ width: '18rem' }}>
                        <Card.Img variant="top" src={recipe.image} />
                        <Card.Body>
                        <Card.Title>{recipe.title}</Card.Title>
                        <Button basic color='pink' content="Full recipe" id={recipe.id} onClick={select} />
                        </Card.Body>
                    </Card>
                )
            }
            setDisplay(display)
        })
    }, [])
    return(
        <>
        <Jumbotron>
            <h1>Recipe app</h1>
            <p>In this app you can find recipes for any food you like and get some tips to lose weight</p>
        </Jumbotron>
        <h1 style={{display: "flex" , justifyContent: "center"}} id="tx">Featured</h1>
        <div style={{display: 'flex' , justifyContent: 'center' , flexWrap: 'wrap'}}>
        {display}
        </div>
        
        
        </>
    )
}
export default Home